const form = document.querySelector("#registrar-actividad");
const actividad = document.querySelector("#todo");
const tipoActividad = document.querySelector("#Tipo-actividad");

form.addEventListener("guardar",(event)=>{
    let listaActividad;
    listaActividad.push(actividad.value +" "+tipoActividad);
    alert("hola");
    document.querySelector("div").innerHTML = JSON.stringify(listaActividad);
})